        display: block;
        background: #fff;
        padding: 15px 0;
        width: 100%;
        text-align: center;
        border: 1px solid #ddd;
    }

    /* Change menu item's width to 100% */
    nav ul li {
        display: block;
        width: 100%;
        padding: 7px 0;
    }

    nav a {
        padding: 5px 0;
    }

    nav a:hover {
        color: #3369e7;
    }

